.projects {
	background: $light-gray;
	padding: 8rem 0;
	&__description {
		font-weight: 300;
		font-size: 1.25rem;
		line-height: 2.25rem;
		color: $gray;
		max-width: 580px;
		margin: 0 0 4rem;
	}
	&__title {
		font-size: 2.25rem;
		margin: 0 auto 1rem;
		font-style: normal;
		font-weight: 600;
		padding: 1rem 16px;
		max-width: 1216px;
	}
}

.projects-list {
	display: grid;
	grid-template-columns: 1fr 1fr;
	column-gap: 1.5rem;
	row-gap: 1.5rem;
	margin: 0;
	padding: 0;
	list-style-type: none;
	@media (max-width: $mobile) {
		grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
	}
}

.project {
	background: #fff;
	box-shadow: 0px 0px 16px rgba(24, 41, 67, 0.07);
	border-radius: 8px;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	&__container {
		padding: 3rem;
	}
	&__description {
		font-style: normal;
		font-weight: 300;
		font-size: 1.125rem;
		line-height: 2rem;
		margin: 0;
	}
	&__footer {
		background: $light-gray;
		padding: 1rem 3rem;
		text-align: right;
	}
	&__link {
		color: $beige;
		font-weight: 500;
		font-size: 1.125rem;
		text-decoration: none;
		line-height: 2rem;
		display: inline-flex;
		align-items: center;
		& + & {
			margin-left: 50px;
			position: relative;
			&::after {
				content: "";
				height: 2rem;
				width: 2px;
				background: #cbd2d9;
				display: block;
				border-radius: 2px;
				position: absolute;
				left: -26px;
				top: 2px;
				pointer-events: none;
			}
		}
		&::before {
			content: "";
			display: block;
			margin-right: 8px;
			width: 24px;
			height: 24px;
			background: url("../assets/img/demo.svg") center center no-repeat;
		}
		&--code::before {
			background: url("../assets/img/source.svg") center center no-repeat;
		}
	}
	&__title {
		font-style: normal;
		font-weight: 600;
		font-size: 1.25rem;
		line-height: 2.25rem;
		margin: 1rem 0 0.5rem;
	}
}
